div {
    box-sizing: border-box;
}

body {
    margin: 0px;
    background: gray;
}

.companyname {
    margin-right: 100px;
    text-align: center;
}

.menu {
    margin-right: 30px;
}

#main {
    background-color: black;
    height: 800px;
    background-image: url(./image/cryptocurrency-3085139_1280.jpeg);
    background-size: cover;
    background-position: center;
    background-attachment: scroll;
}
.blank {
    height: 300px;
}
.main-title {
    color: white;
    font-size: 40px;
    border-top: 400px;
    margin-left: 50px;
    box-sizing: border-box;
}

.main-title:hover {
    color: rgb(220, 185, 185);
}

#project {
    background-color: gray;
    height: 840px;
    text-align: center;
    font-size: xx-large;
}

.project {
    height: 775px;
    width: 50%;
    display: inline-block;
    line-height: left;
}

.coin {
    background-color: olive;
    font-size: large;
    color: white;
}

.bot {
    background-color: rgb(128, 48, 48);
    font-size: large;
    color: white;
}

footer {
    background-color: grey;
    text-align: center;
    font-size: x-large;
    margin-top: 10px;
    margin-bottom: 20px;
}

.project_title {
    margin-top: 100px;
    font-weight: bold;
    font-size: 80px;
    margin-bottom: 50px;
}
