#member {
    background-color: grey;
    max-height: 1000px;
    text-align: center;
    font-size: xx-large;
}

.member {
    margin: auto;
}
.member_title {
    margin-top: 100px;
    font-weight: bold;
    font-size: 80px;
}

.eunjae {
    width: 100%;
    height: 300px;
    background-color: #e4e4e4;
    font-size: x-large;
    color: rgb(0, 0, 0);
    border: 5px dotted burlywood;
    border-radius: 15px;
    float: left;
    margin-top: 50px;
    margin-bottom: 100px;
}

.person_image {
    display: flex;
    width: 30%;
    height: 50%;
    display: block;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
}

.future_person_image {
    display: flex;
    width: 60%;
    height: 70%;
    display: block;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
}

.profile {
    margin-top: 40px;
    font-size: medium;
}

.ele {
    align-items: center;
    margin-left: 20px;
    margin-right: 20px;
    flex-direction: row;
}
